import gsap from 'gsap';
import { useEffect } from "react";
import RegulaminItem from "./components/RegulaminItem";
import doc from './images/UMOWA.docx'

const content = [
    ["I","Wypoyczanie sprztu odbywa si na zasadach odpatnoci zgodnie z cennikiem. "],
    ["II","Aby wypoyczy sprzt naley okaza dokument tosamoci ze zdjciem (dowd osobisty, paszport, prawo jazdy). Nie obsuguje si osb niepenoletnich, pod wpywem alkoholu lub innych rodkw odurzajcych. "],
    ["III","Osoby nieletnie mog pyn tylko pod opiek osb penoletnich, ktre ponosz pen odpowiedzialno za nie, za ich zachowanie i ewentualne szkody. "],
    ["IV","Osoba wypoyczajca odpowiedzialna jest za powierzony sprzt pywajcy i ponosi koszty uszkodzenia lub zaginicia sprztu. "],
    ["V","Na kadej jednostce musi si znajdowa tyle kamizelek ratunkowych lub asekuracyjnych ile osb si na niej znajduje, obowizkowo naley je zaoy na siebie podczas pywania. "],
    ["VI","Wypoyczalnia nie zajmuje si organizacj spyww kajakowych a jedynie wypoycza sprzt. Spywy naley organizowa samodzielnie i na wasn odpowiedzialno. "],
    ["VII","Odbir i zwrot sprztu odbywa si w miejscu wczeniej ustalonym przez Wypoyczajcego oraz Wypoyczalni. "],
    ["VIII","Sprzt zwracany Wypoyczalni powinien by w stanie umoliwiajcym ponowne wypoyczenie."],
    ["IX","Na niezwrcenie sprztu w zadeklarowanym i opaconym terminie uytkownik ponosi dodatkow opat w wysokoci 100% dobowej nalenoci za wypoyczenie."],
    ["X","Wypoyczalnia nie ponosi adnej odpowiedzialnoci z tytuu nieszczliwych wypadkw, utraty zdrowia lub mierci uczestnikw spyww. "],
    ["XI","Osoby naruszajce regulamin wypoyczalni bd zobowizane do zakoczenia pywania wypoyczonym sprztem niezalenie od czasu przebywania na wodzie bez prawa zwrotu pienidzy. "],
    ["XII","Wczeniejsze, ni to ustalono, terminy, miejsca i godziny zakoczenia spyww musz by zaakceptowane przez Wypoyczalni i mog by zrealizowane wycznie na jej warunkach. "],
    ["XIII","Wypoyczenie sprztu jest rwnoznaczne z zaakceptowaniem warunkw zawartych w regulaminie."],
    
]



const Regulamin = () => {
    useEffect(()=>{
        const numbers = document.querySelectorAll('.regulamin__number')
        const text = document.querySelectorAll('.regulamin__text')
        gsap.set([ numbers, text], {autoAlpha:0})
        const tl = gsap.timeline({defaults: {ease: 'power3.inOut'}});
        tl
        .addLabel('start')
        .fromTo(numbers ,{autoAlpha: 0}, {autoAlpha: 1, stagger:.4,  duration: .5}, 'start')
        .fromTo(text ,{y: -5, autoAlpha: 0}, {autoAlpha: 1, y: 0, stagger:.4,  duration: .5}, 'start+=0.2')
    },[])


    return ( 
        <div className="regulamin">
        <h2 className="pageTitle">Regulamin</h2>

        <div className="regulamin__content">
        {content.map((item)=>(
            <RegulaminItem nr={item[0]} content={item[1]} />
            ))}




        </div>

           <a download href={doc}> <button className="regulamin__umowa">UMOWA</button> </a>
    </div> );
}
 
export default Regulamin;